.PhoneInput {
    display: flex;
    align-items: center;
    height: 16px;
	margin-bottom: 0px;
	border: none;
}

.PhoneInputInput {
  border: 1px solid #aaa;
	outline: none;
	height: 16px;
	width: 100%;
	color:#808080;
	margin-bottom: 0px;
	margin-top: 2px;
	text-indent: 10px;
	padding-top: 5px;
	padding-bottom: 5px;
  	font-family: 'AvenirMedium';
	font-size: 13px;
	padding-left:0;
	padding-right:0;
}

.PhoneInputCountry {
  margin-bottom: 0px;
}

.validation-failed input {
  border-color: red !important;
}
